@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

[class*="scena-"] {
    font-family: "Open Sans", sans-serif;
}

.scena-editor {
    position: relative;
    width: 100%;
    height: 100%;
    font-family: sans-serif;
    --mainColor: #4af;
    --back1: #1a1a1a;
    --back2: #2a2a2a;
    --back3: #333;
    --back4: #444;
    --back5: #555;
    --back6: #666;
    transform-style: preserve-3d;
}

.scena-editor .scena-viewer {
    position: absolute !important;
    left: calc(30px + var(--menu));
    top: 30px;
    width: calc(100% - 30px - var(--menu) - var(--tabs));
    height: calc(100% - 30px);
}

.scena-viewport {
    width: 400px;
    height: 600px;
    border: 1px solid #eee;
    box-sizing: border-box;
}

.scena-guides {
    position: absolute !important;
    top: 0;
    left: 0;
    transform: translateZ(1px);
}

.scena-guides.scena-horizontal {
    left: calc(var(--menu) + 30px);
    width: calc(100% - 30px - var(--menu) - var(--tabs));
    height: 30px !important;
}

.scena-guides.scena-vertical {
    top: 30px;
    left: var(--menu);
    height: calc(100% - 30px);
    width: 30px !important;
}

.scena-reset {
    position: absolute !important;
    background: var(--back3);
    width: 30px;
    height: 30px;
    z-index: 1;
    border-right: 1px solid var(--back4);
    border-bottom: 1px solid var(--back4);
    box-sizing: border-box;
    cursor: pointer;
    left: var(--menu);
    top: 0px;
}

.scena-overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background: rgba(0, 0, 0, 0.2);
}

.scena-editor {
    --menu: 45px;
}

.scena-menu {
    position: absolute;
    top: 0;
    width: var(--menu);
    height: 100%;
    background: var(--back2);
    box-sizing: border-box;
    padding: 15px 7px 0px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.scena-menu-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 15px 7px 0px;
    width: 100%;
    box-sizing: border-box;
}

.scena-menu svg, .scena-menu .scena-i {
    pointer-events: none;
}

.scena-icon {
    position: relative;
    display: block;
    font-size: 0;
    padding: 3px;
    box-sizing: border-box;
    cursor: pointer;
    border: 1px solid transparent;
    margin-bottom: 5px;
    border-radius: 3px;
    transition: all ease 0.2s;
}

.scena-menu .scena-selected {
    background: var(--mainColor);
    border-color: var(--back1);
}

.scena-icon.scena-selected>svg path,
.scena-icon.scena-selected>svg ellipse,
.scena-sub-icon.scena-selected path,
.scena-sub-icon.scena-selected ellipse {
    fill: #fff;
    stroke: #fff;
}

.scena-icon .scena-extends-icon {
    position: absolute;
    right: 2px;
    bottom: 2px;
    border-bottom: 5px solid #eee;
    border-right: 0;
    border-left: 5px solid transparent;
}

.scena-extends-container {
    position: absolute;
    left: 110%;
    top: -30px;
    background: var(--back2);
    /* width: 200px;
    height: 200px; */
    border-radius: 5px;
    z-index: 1;
    transform: translate(10px) translateZ(2px);
    box-shadow: 1px 1px 2px var(--back1);
    display: none;
}

.scena-sub-icon {
    white-space: nowrap;
    padding: 0px 7px;
    margin: 7px 10px;
}

.scena-sub-icon .scena-icon {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 25px;
    height: 25px;
    margin: 0;
}

.scena-sub-icon-label {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    color: white;
    font-size: 14px;
    font-weight: bold;
    padding: 0;
    margin: 0;
    margin-left: 5px;
}


.scena-keyboard {
    background: #fff;
    border-radius: 3px;
    width: 90%;
    height: 15px;
    margin: 3px 0px 7px;
    text-align: center;
    box-sizing: border-box;
    padding: 2px;
}

.scena-key {
    display: inline-block;
    width: 2px;
    height: 2px;
    background: var(--back2);
    margin: 1px;
}
.scena-space {
    display: inline-block;
    width: 12px;
    height: 2px;
    background: var(--back2);
    margin: 1px;
    border-radius: 1px;
}

.scena-overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    transform-style: preserve-3d;
    transform: translateZ(15px);
    background: rgba(0, 0, 0, 0.2);
}

.scena-popup {
    position: absolute;
    max-width: 600px;
    width: 80%;
    max-height: 1000px;
    padding: 30px;
    height: 80%;
    overflow: auto;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 14px;
    background: var(--back3);
    box-sizing: border-box;
    color: #fff;
}
.scena-popup h2 {
    padding: 0;
    margin: 0;
    padding: 4px;
}
.scena-popup .scena-close {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 40px;
    height: 40px;
    cursor: pointer;
}
.scena-popup .scena-close:before, .scena-popup .scena-close:after {
    content: "";
    position: absolute;
    width: 30px;
    height: 1px;
    background: #ddd;
    top: 50%;
    left: 50%;
}
.scena-popup .scena-close:hover:before, .scena-popup .scena-close:hover:after {
    height: 2px;
    background: #fff;
    border-radius: 1px;
}
.scena-popup .scena-close:before {
    transform: translate(-50%, -50%) rotate(45deg);
}
.scena-popup .scena-close:after {
    transform: translate(-50%, -50%) rotate(-45deg);
}
.scena-popup ul {
    list-style: none;
    padding: 0;
}
.scena-popup li {
    padding: 4px 10px;
    border-bottom: 1px solid var(--back2);
    font-weight: bold;;
}
.scena-popup li:hover {
    background: #fff;
    color: var(--back2);
}
.scena-popup p {
    position: relative;
    padding: 2px 0px;
}
.scena-popup strong {
    position: absolute;
    right: 0;
    text-transform: uppercase;
}
.scena-popup strong span {
    display: inline-block;
    padding: 2px 4px;
    border: 1px solid #fff;
    vertical-align: top;
    margin: 0px 5px;
}

.scena-editor {
    --tabs: 40px;
    --tab: 200px;
}

.scena-tabs {
    position: absolute;
    right: 0;
    width: 80px;
    height: 100%;
    background: var(--back2);
    z-index: 10;
    transform: translateZ(10px);
    box-sizing: border-box;
    padding-top: 30px;
    transition: width ease 0.2s;
    text-align: left;
}

.scena-tab-icon {
    position: relative;
    margin: 5px;
}

.scena-tab-icon-label {
    position: relative;
    color: #fff;
    font-size: 11px;
    font-weight: bold;
    word-break: break-all;
    padding: 5px;
    width: 100%;
    line-height: 20px;
    height: 30px;
    box-sizing: border-box;
    border-radius: 3px;
    text-align: left;
    overflow: hidden;
}

.scena-tab-icon-label span {
    display: inline-block;
    pointer-events: none;
    font-size: 11px;
}

.scena-tab-icon.scena-selected .scena-tab-icon-label {
    background: var(--mainColor);
}

.scena-tab {
    position: absolute;
    right: 100%;
    top: 0;
    transform: translate(-5px);
    width: var(--tab);
    padding: 0px 10px 10px;
    background: var(--back2);
    border-bottom: 1px solid var(--back1);
    text-align: left;
}

.scena-tab h2 {
    margin: 0;
    color: white;
    font-weight: bold;
    font-size: 14px;
    padding: 8px 0px;
}

.scena-tab-line {
    position: relative;
    display: block;
}

@media screen and (max-width: 800px) {
    .scena-tabs {
        width: 40px;
    }
    .scena-tabs.scena-over {
        width: 80px;
    }
    .scena-tab-icon-label span:first-letter {
        visibility: visible;
        font-size: 11px;
    }
    .scena-tab-icon-label {
        text-align: center;
    }
    .scena-tabs.scena-over .scena-tab-icon-label {
        text-align: left;
    }
    .scena-tab-icon-label span {
        visibility: hidden;
        font-size: 0;
        overflow: hidden;
    }
    .scena-tabs.scena-over .scena-tab-icon-label span {
        font-size: 11px;
        visibility: visible;
    }
}
.scena-input {
    position: relative;
    text-align: left;
    -moz-appearance: none;
         appearance: none;
    -webkit-appearance: none;
    outline: none;
    display: block;
    width: 100%;
    height: 30px;
    background: transparent;
    color: var(--mainColor);
    font-weight: bold;
    background: none;
    border: 0;
    padding: 5px;
    box-sizing: border-box;
    background: var(--back1);
    font-size: 12px;
}
.scena-select {
    position: relative;
    -moz-appearance: none;
         appearance: none;
    -webkit-appearance: none;
    outline: none;
    display: block;
    width: 100%;
    height: 30px;
    padding: 5px;
    background: transparent;
    color: var(--mainColor);
    font-weight: bold;
    background: var(--back1);
    border: 0;
    box-sizing: border-box;
    text-align: center;
}

.scena-tab-input {
    position: relative;
    box-sizing: border-box;
    padding: 2px;
    display: inline-block;
    vertical-align: top;
}
.scena-tab-input h3, .scena-tab-input h4 {
    color: white;
    margin: 0;
    padding: 7px 5px;
    font-size: 12px;
    font-weight: bold;
    display: inline-block;
}
.scena-tab-input h4 {
    font-size: 11px;
}
.scena-tab-input.scena-full {
    width: 100%;
}
.scena-tab-input.scena-half {
    width: 50%;
}
.scena-tab-input.scena-third {
    width: 33%;
}
.scena-tab-input.scena-twothird {
    width: 66%;
}
.scena-tab-input.scena-half:nth-child(2n + 1) {
    padding-right: 5px;
}
.scena-tab-input.scena-half:nth-child(2n + 2) {
    padding-left: 5px;
}

.scena-anchor-input {
    position: relative;
    width: 30px;
    height: 35px;
    margin: auto;
    box-sizing: border-box;
}
.scena-anchor-input-background {
    position: relative;
    width: 30px;
    height: 30px;
    background: var(--back1);
}
.scena-anchor-control {
    position: absolute;
    width: 8px;
    height: 8px;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    padding: 1px;
    box-sizing: border-box;
}
.scena-anchor-control:before {
    content: "";
    position: relative;
    display: block;
    background: var(--back5);
    width: 100%;
    height: 100%;
}
.scena-anchor-control.scena-anchor-selected {
    padding: 0px;
}
.scena-anchor-control.scena-anchor-selected:before {
    background: white;
}

.scena-anchor-control.scena-anchor-n {
    top: 0;
}
.scena-anchor-control.scena-anchor-s {
    top: 100%;
}
.scena-anchor-control.scena-anchor-w {
    left: 0;
}
.scena-anchor-control.scena-anchor-e {
    left: 100%;
}

.scena-fold-icon {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 10px;
    height: 14px;
}
.scena-fold-icon:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-top: 4px solid #fff;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
}
.scena-fold-icon.scena-fold:before {
    border-right: 0;
    border-left: 4px solid #fff;
    border-top: 3px solid transparent;
    border-bottom: 3px solid transparent;
}
.scena-folder {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.scena-file {
    border-bottom: 1px solid var(--back1);
}
.scena-file.scena-selected {
    background: var(--mainColor);
}

.scena-frame-tab {
    overflow: auto;
    max-height: 300px;
}

.scena-align {
    position: relative;
    width: 25px;
    height: 25px;
    /* background: #f55; */
    display: inline-block;
    margin-right: 5px;
}
.scena-align:hover {
    cursor: pointer;
}
.scena-align:hover * {
    background: white;
}
.scena-align-line,
.scena-align-element1,
.scena-align-element2 {
    position: absolute;
    left: 50%;
    top: 50%;
    background: var(--back6);
    transform: translate(-50%, -50%);
}
.scena-align-vertical .scena-align-line {
    width: 1px;
    height: 18px;
}
.scena-align-vertical .scena-align-element1 {
    width: 10px;
    height: 5px;
    margin-top: -3.5px;
}
.scena-align-vertical .scena-align-element2 {
    width: 14px;
    height: 5px;
    margin-top: 3.5px;
}
.scena-align-vertical.scena-align-start .scena-align-line {
    margin-left: -7px;
}
.scena-align-vertical.scena-align-start .scena-align-element1 {
    margin-left: -2px;
}

.scena-align-vertical.scena-align-end .scena-align-line {
    margin-left: 7px;
}
.scena-align-vertical.scena-align-end .scena-align-element1 {
    margin-left: 2px;
}



.scena-align-horizontal .scena-align-line {
    height: 1px;
    width: 18px;
}
.scena-align-horizontal .scena-align-element1 {
    height: 10px;
    width: 5px;
    margin-left: -3.5px;
}
.scena-align-horizontal .scena-align-element2 {
    height: 14px;
    width: 5px;
    margin-left: 3.5px;
}
.scena-align-horizontal.scena-align-start .scena-align-line {
    margin-top: -7px;
}
.scena-align-horizontal.scena-align-start .scena-align-element1 {
    margin-top: -2px;
}

.scena-align-horizontal.scena-align-end .scena-align-line {
    margin-top: 7px;
}
.scena-align-horizontal.scena-align-end .scena-align-element1 {
    margin-top: 2px;
}


.scena-color-input {
    position: relative;
}
.scena-color-picker {
    position: absolute;
    top: 100%;
    right: 0;
    transform: translateY(10px) translateZ(1px);
    z-index: 10;
}

.scena-color-background {
    position: relative;
    width: 30px;
    height: 30px;
    background: #4af;
    border-radius: 5px;
    display: inline-block;
    vertical-align: top;
    margin-right: 5px;
}
.scena-color-background+.scena-input {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 40px);
}



html, body, #root, .app, .editor {
    position: relative;
    width: 100%;
    height: 100%;
}
.bottom {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
    padding: 5px;
    background: #222;
    box-sizing: border-box;
    text-align: center;
}
.bottom a {
    position: relative;
    display: inline-block;
    color: white;
    /* text-decoration: none; */
    font-weight: bold;
    font-size: 13px;
    padding: 15px 20px;
    box-sizing: border-box;
}
.badges img {
    padding: 2px;
}
.badgets a {
    margin: 2px;
}

